"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Textarea } from "@/components/ui/textarea"
import { 
  ChevronLeft, Loader2, Target, Sparkles, Trash2, 
  Edit3, CheckCircle2, BrainCircuit, Wallet, ArrowRight, History, X, Clock,
  Briefcase, Calendar
} from "lucide-react"

export default function UserGoalPage() {
  const router = useRouter()
  const [isLoading, setIsLoading] = useState(true)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [exists, setExists] = useState(false)
  
  const [step, setStep] = useState<'view' | 'form' | 'deep_analysis' | 'history'>('view')
  const [aiResult, setAiResult] = useState<string | null>(null)

  const [formData, setFormData] = useState({
    age: "", job: "", capital: "", monthlyIncome: "", fixedExpenses: ""
  })

  const [deepAnalysisData, setDeepAnalysisData] = useState({
    goalTitle: "",
    monthlySaving: "",
    investmentStyle: "공격형", 
    customMessage: ""
  })

  const [reports, setReports] = useState<any[]>([])

  const fetchInitialData = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    if (!token) return router.push("/login")

    try {
      const goalRes = await fetch("http://34.50.7.8:3000/v1/usergoal", {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const goalData = await goalRes.json()

      if (goalRes.ok && goalData.isSuccess && goalData.result && goalData.result.age) {
        setFormData({
          age: String(goalData.result.age),
          job: goalData.result.job,
          capital: String(goalData.result.capital),
          monthlyIncome: String(goalData.result.monthlyIncome),
          fixedExpenses: String(goalData.result.fixedExpenses || "")
        })
        setExists(true)
        setStep('view')
      } else {
        setExists(false)
        setStep('form')
      }

      const reportRes = await fetch("http://34.50.7.8:3000/v1/usergoal/report/all", {
        method: "POST",
        headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
        body: JSON.stringify({})
      })
      const reportData = await reportRes.json()
      if (reportRes.ok && reportData.isSuccess) {
        setReports(reportData.result || [])
      }
    } catch (error) { 
      setStep('form')
    } finally { 
      setIsLoading(false) 
    }
  }, [router])

  useEffect(() => { fetchInitialData() }, [fetchInitialData])

  const handleDeleteBaseGoal = async () => {
    if(!confirm("목표를 삭제하면 연관된 모든 AI 분석 히스토리도 함께 삭제됩니다. 계속하시겠습니까?")) return
    const token = localStorage.getItem("accessToken")
    setIsSubmitting(true)
    try {
      if (reports.length > 0) {
        await Promise.all(
          reports.map(report => 
            fetch(`http://34.50.7.8:3000/v1/usergoal/report?reportId=${report.reportId}`, {
              method: "DELETE",
              headers: { "Authorization": `Bearer ${token}` }
            })
          )
        )
      }
      const res = await fetch("http://34.50.7.8:3000/v1/usergoal", {
        method: "DELETE",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if(res.ok && data.isSuccess) {
        alert("모든 데이터가 삭제되었습니다.")
        setExists(false)
        setReports([])
        setFormData({ age: "", job: "", capital: "", monthlyIncome: "", fixedExpenses: "" })
        setStep('form')
      }
    } catch (e) {
      alert("삭제 중 오류가 발생했습니다.")
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleDeleteReport = async (reportId: string) => {
    if(!confirm("이 분석 결과만 삭제하시겠습니까?")) return
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`http://34.50.7.8:3000/v1/usergoal/report?reportId=${reportId}`, {
        method: "DELETE",
        headers: { "Authorization": `Bearer ${token}` }
      })
      if(res.ok) {
        setReports((prev) => prev.filter((r) => r.reportId !== reportId))
      }
    } catch (e) { alert("통신 오류") }
  }

  const handleSaveInfo = async () => {
    const token = localStorage.getItem("accessToken")
    setIsSubmitting(true)
    try {
      const response = await fetch("http://34.50.7.8:3000/v1/usergoal", {
        method: exists ? "PATCH" : "POST",
        headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
        body: JSON.stringify({
          age: Number(formData.age),
          job: formData.job,
          capital: Number(formData.capital),
          monthlyIncome: Number(formData.monthlyIncome),
          fixedExpenses: Number(formData.fixedExpenses)
        })
      })
      if (response.ok) {
        alert("저장되었습니다.")
        setExists(true)
        setStep('view')
      }
    } catch (e) { alert("통신 오류") }
    finally { setIsSubmitting(false) }
  }

  const handleCreateReport = async () => {
    const token = localStorage.getItem("accessToken")
    setIsSubmitting(true)
    try {
      const response = await fetch("http://34.50.7.8:3000/v1/usergoal/report/create", {
        method: "POST",
        headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
        body: JSON.stringify({ 
          question: `목표: ${deepAnalysisData.goalTitle}. 저축액: ${deepAnalysisData.monthlySaving}. 요구사항: ${deepAnalysisData.customMessage}`
        })
      })
      const data = await response.json()
      if (response.ok && data.isSuccess) {
        setAiResult(data.result.resultReport)
        fetchInitialData() 
      }
    } catch (e) { alert("분석 생성 오류") }
    finally { setIsSubmitting(false) }
  }

  if (isLoading) return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-slate-50">
      <Loader2 className="h-10 w-10 animate-spin text-primary mb-4" />
      <p className="text-slate-500 font-medium">재무 정보를 불러오고 있습니다...</p>
    </div>
  )

  return (
    <div className="min-h-screen bg-slate-50 text-slate-900">
      {/* HEADER */}
      <header className="border-b bg-white h-16 flex items-center px-4 sticky top-0 z-50 justify-between shadow-sm">
        <div className="flex items-center">
          <Button variant="ghost" size="icon" onClick={() => step === 'view' ? router.push("/dashboard") : setStep('view')} className="text-slate-600">
            <ChevronLeft className="h-5 w-5" />
          </Button>
          <h1 className="ml-2 text-base font-bold text-slate-800">재무 목표 설정</h1>
        </div>
        {exists && (
          <Button variant="ghost" size="sm" onClick={() => setStep('history')} className="font-semibold gap-1.5 text-slate-500 hover:text-primary">
            <History className="h-4 w-4" /> 히스토리
          </Button>
        )}
      </header>

      <main className="container mx-auto max-w-xl px-4 py-8">
        {/* AI RESULT MODAL */}
        {aiResult && (
          <div className="fixed inset-0 z-[100] bg-slate-900/60 backdrop-blur-sm flex items-center justify-center p-4">
            <Card className="w-full max-w-lg max-h-[85vh] flex flex-col bg-white border-none shadow-2xl rounded-2xl overflow-hidden">
              <div className="p-5 border-b flex justify-between items-center bg-white">
                <div className="flex items-center gap-2 text-primary font-bold"><Sparkles className="h-5 w-5" /> AI 분석 리포트</div>
                <Button variant="ghost" size="icon" onClick={() => setAiResult(null)}><X className="h-5 w-5" /></Button>
              </div>
              <div className="p-6 overflow-y-auto text-sm leading-relaxed whitespace-pre-wrap font-medium text-slate-600">{aiResult}</div>
              <div className="p-5 border-t bg-slate-50"><Button className="w-full font-bold h-12" onClick={() => setAiResult(null)}>리포트 닫기</Button></div>
            </Card>
          </div>
        )}
        
        {/* VIEW MODE */}
        {step === 'view' && exists && (
          <div className="space-y-6 animate-in fade-in duration-500">
            <div className="text-center mb-8">
              <div className="h-20 w-20 bg-primary/10 rounded-full flex items-center justify-center mx-auto mb-4 border-4 border-white shadow-sm">
                <CheckCircle2 className="h-10 w-10 text-primary" />
              </div>
              <h2 className="text-2xl font-bold text-slate-900 mb-1">내 재무 프로필</h2>
              <p className="text-sm text-slate-500 font-medium">{formData.job} • {formData.age}세</p>
            </div>

            <Card className="p-0 overflow-hidden border-none shadow-md bg-white">
              <div className="p-6 bg-primary text-white">
                <div className="flex items-center gap-3 mb-2">
                  <BrainCircuit className="h-6 w-6" />
                  <span className="font-bold text-lg">AI 재무 정밀 분석</span>
                </div>
                <p className="text-xs text-white/80 leading-relaxed">입력된 데이터를 바탕으로 AI가 최적의 저축 및 투자 전략을 제안합니다.</p>
              </div>
              <div className="p-4">
                <Button 
                  className="w-full h-14 bg-slate-900 hover:bg-slate-800 text-white font-bold rounded-xl"
                  onClick={() => setStep('deep_analysis')}
                >
                  새 분석 리포트 생성 <ArrowRight className="ml-2 h-4 w-4" />
                </Button>
              </div>
            </Card>

            <div className="grid grid-cols-2 gap-4">
              <Button variant="outline" className="h-14 font-bold border-slate-200 bg-white shadow-sm" onClick={() => setStep('form')}><Edit3 className="mr-2 h-4 w-4" /> 정보 수정</Button>
              <Button variant="outline" className="h-14 font-bold border-slate-200 bg-white text-rose-500 hover:bg-rose-50 shadow-sm" onClick={handleDeleteBaseGoal} disabled={isSubmitting}>
                {isSubmitting ? <Loader2 className="animate-spin" /> : <><Trash2 className="mr-2 h-4 w-4" /> 전체 삭제</>}
              </Button>
            </div>

            <Card className="p-6 bg-white border-none shadow-sm space-y-4">
              <h3 className="text-xs font-bold text-slate-400 uppercase tracking-wider flex items-center gap-1.5"><Wallet className="h-4 w-4" /> 현재 자산 상태</h3>
              <div className="grid grid-cols-2 gap-4">
                <div className="p-4 rounded-xl bg-slate-50 border border-slate-100">
                  <p className="text-[10px] text-slate-400 font-bold uppercase mb-1">총 자산</p>
                  <p className="text-lg font-bold text-slate-900">₩{Number(formData.capital).toLocaleString()}</p>
                </div>
                <div className="p-4 rounded-xl bg-slate-50 border border-slate-100">
                  <p className="text-[10px] text-slate-400 font-bold uppercase mb-1">월 수입</p>
                  <p className="text-lg font-bold text-primary">₩{Number(formData.monthlyIncome).toLocaleString()}</p>
                </div>
              </div>
            </Card>
          </div>
        )}

        {/* HISTORY MODE */}
        {step === 'history' && (
          <div className="space-y-6 animate-in slide-in-from-right-8 duration-500">
            <div className="flex items-center gap-2 mb-2">
              <History className="h-6 w-6 text-primary" />
              <h2 className="text-xl font-bold">분석 히스토리</h2>
            </div>
            <div className="space-y-3">
              {reports.length > 0 ? reports.map((report) => (
                <Card key={report.reportId} className="p-4 bg-white border border-slate-100 shadow-sm hover:shadow-md transition-shadow">
                  <div className="flex justify-between items-center">
                    <div className="cursor-pointer flex-1 pr-4" onClick={() => setAiResult(report.resultReport)}>
                      <p className="text-sm font-bold text-slate-800 truncate mb-1">{report.question?.split('.')[0] || "Financial Report"}</p>
                      <div className="flex items-center gap-2 text-[11px] text-slate-400 font-medium"><Clock className="h-3.5 w-3.5" /> {new Date(report.createdAt).toLocaleDateString()}</div>
                    </div>
                    <Button variant="ghost" size="icon" className="text-slate-300 hover:text-rose-500" onClick={() => handleDeleteReport(report.reportId)}><Trash2 className="h-4 w-4" /></Button>
                  </div>
                </Card>
              )) : (
                <div className="text-center py-20 bg-white rounded-2xl border border-dashed border-slate-200">
                  <p className="text-slate-400 font-medium">생성된 리포트가 없습니다.</p>
                </div>
              )}
            </div>
          </div>
        )}

        {/* FORM MODE */}
        {step === 'form' && (
          <div className="space-y-6 animate-in slide-in-from-bottom-6 duration-500">
            <div className="text-center mb-6">
              <h2 className="text-2xl font-bold">기본 정보 설정</h2>
              <p className="text-sm text-slate-500 mt-1">정확한 분석을 위해 상세히 입력해주세요.</p>
            </div>
            <Card className="p-6 space-y-5 shadow-lg bg-white border-none rounded-2xl">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-1.5">
                  <label className="text-xs font-bold text-slate-500 ml-1 flex items-center gap-1.5"><Calendar className="h-3.5 w-3.5" /> 나이</label>
                  <Input type="number" value={formData.age} onChange={e => setFormData({...formData, age: e.target.value})} className="h-12 font-bold bg-slate-50 border-slate-100 focus:bg-white" placeholder="30" />
                </div>
                <div className="space-y-1.5">
                  <label className="text-xs font-bold text-slate-500 ml-1 flex items-center gap-1.5"><Briefcase className="h-3.5 w-3.5" /> 직업</label>
                  <Input value={formData.job} onChange={e => setFormData({...formData, job: e.target.value})} className="h-12 font-bold bg-slate-50 border-slate-100 focus:bg-white" placeholder="회사원" />
                </div>
              </div>
              <div className="space-y-1.5">
                <label className="text-xs font-bold text-slate-500 ml-1">현재 총 자산 (원)</label>
                <Input type="number" value={formData.capital} onChange={e => setFormData({...formData, capital: e.target.value})} className="h-12 font-bold bg-slate-50 border-slate-100 focus:bg-white" placeholder="0" />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-1.5">
                  <label className="text-xs font-bold text-slate-500 ml-1">월 평균 수입</label>
                  <Input type="number" value={formData.monthlyIncome} onChange={e => setFormData({...formData, monthlyIncome: e.target.value})} className="h-12 font-bold bg-slate-50 border-slate-100 focus:bg-white" placeholder="0" />
                </div>
                <div className="space-y-1.5">
                  <label className="text-xs font-bold text-slate-500 ml-1">월 고정 지출</label>
                  <Input type="number" value={formData.fixedExpenses} onChange={e => setFormData({...formData, fixedExpenses: e.target.value})} className="h-12 font-bold bg-slate-50 border-slate-100 focus:bg-white text-rose-600" placeholder="0" />
                </div>
              </div>
              <Button className="w-full h-14 text-base font-bold mt-2 rounded-xl" onClick={handleSaveInfo} disabled={isSubmitting}>
                {isSubmitting ? <Loader2 className="animate-spin mr-2" /> : "프로필 저장하기"}
              </Button>
            </Card>
          </div>
        )}

        {/* DEEP ANALYSIS MODE */}
        {step === 'deep_analysis' && (
          <div className="space-y-6 animate-in slide-in-from-right-8 duration-500">
            <div className="flex items-center gap-2 mb-2">
              <Sparkles className="h-6 w-6 text-primary" />
              <h2 className="text-xl font-bold">새로운 목표 분석</h2>
            </div>
            <Card className="p-6 space-y-6 shadow-lg bg-white border-none rounded-2xl">
              <div className="space-y-5">
                <div className="space-y-1.5">
                  <label className="text-xs font-bold text-primary ml-1 uppercase tracking-wider">희망 재무 목표</label>
                  <Input value={deepAnalysisData.goalTitle} onChange={e => setDeepAnalysisData({...deepAnalysisData, goalTitle: e.target.value})} className="h-14 font-bold border-primary/20 bg-primary/5 focus:bg-white text-base" placeholder="예: 3년 내 아파트 전세자금 마련" />
                </div>
                <div className="space-y-1.5">
                  <label className="text-xs font-bold text-slate-500 ml-1">월 추가 저축 가능 금액</label>
                  <Input type="number" value={deepAnalysisData.monthlySaving} onChange={e => setDeepAnalysisData({...deepAnalysisData, monthlySaving: e.target.value})} className="h-12 font-bold bg-slate-50 border-slate-100" placeholder="0" />
                </div>
                <div className="space-y-2">
                  <label className="text-xs font-bold text-slate-500 ml-1">AI 전달 사항 (선택)</label>
                  <Textarea className="min-h-[120px] bg-slate-50 border-slate-100 font-medium text-sm focus:bg-white" value={deepAnalysisData.customMessage} onChange={e => setDeepAnalysisData({...deepAnalysisData, customMessage: e.target.value})} placeholder="AI가 분석 시 참고할 특이사항이나 투자 성향을 적어주세요." />
                </div>
              </div>
              <Button className="w-full h-16 text-lg font-bold bg-primary text-white shadow-md hover:shadow-lg transition-all rounded-xl" onClick={handleCreateReport} disabled={isSubmitting}>
                {isSubmitting ? <Loader2 className="animate-spin mr-2" /> : <span className="flex items-center gap-2">분석 시작하기 <ArrowRight className="h-5 w-5" /></span>}
              </Button>
            </Card>
          </div>
        )}
      </main>
    </div>
  )
}